.ar {
  position: relative;
}

.ar-ab {
  position: relative;
}

.ar::after {
  content: "";
  position: absolute;
  right: -13px;
  top: 18px;
  width: 10px;
  height: 10px;
  border-top: 2px solid #ee1d23;
  border-right: 2px solid #ee1d23;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.ar-ab::after {
  content: "";
  position: absolute;
  right: -13px;
  top: 18px;
  width: 10px;
  height: 10px;
  border-top: 2px solid #ee1d23;
  border-right: 2px solid #ee1d23;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.ar-ab::before {
  content: "";
  position: absolute;
  left: 50%;
  margin-left: -5px;
  bottom: -18px;
  width: 10px;
  height: 10px;
  border-top: 2px solid #ee1d23;
  border-right: 2px solid #ee1d23;
  -webkit-transform: rotate(45deg);
  transform: rotate(135deg);
}

.minus {
  position: relative;
  top: -1px;
}

.sh-burger {
  position: relative;
  display: none;
  width: 34px;
  height: 26px;
  cursor: pointer;
  transition-duration: 0.2s;
}
.sh-burger span,
.sh-burger span::before,
.sh-burger span::after {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 34px;
  height: 2px;
  margin-top: -1px;
  margin-left: -17px;
  background-color: #fff;
}
.sh-burger span::before,
.sh-burger span::after {
  content: "";
  transition: 0.2s;
}
.sh-burger span::before {
  transform: translateY(-12px);
}
.sh-burger span::after {
  transform: translateY(12px);
}
.sh-burger--active {
  background: #F5F6FB;
}
.sh-burger--active span {
  height: 0;
}
.sh-burger--active span::before {
  transform: rotate(45deg);
  background: #323340;
}
.sh-burger--active span::after {
  transform: rotate(-45deg);
  background: #323340;
}

